import React, { useRef } from 'react';
import {
    type AriaButtonProps,
    mergeProps,
    useButton,
    useFocusRing,
} from 'react-aria';
import classNames from 'classnames';

import styles from './CustomTrigger.module.scss';

export interface CustomTriggerProps extends AriaButtonProps {
    className?: string;
}

const CustomTrigger = (props: CustomTriggerProps) => {
    const ref = useRef(null);
    const { buttonProps } = useButton(props, ref);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.btn, props.className, {
        [styles['focus-ring']]: isFocusVisible,
    });

    return (
        <button
            {...mergeProps(buttonProps, focusProps)}
            ref={ref}
            className={classList}
        >
            {props.children}
        </button>
    );
};

export default CustomTrigger;
