import React, { ReactNode, useRef, useState } from 'react';
import { AriaModalOverlayProps, Overlay, useModalOverlay } from 'react-aria';
import { OverlayTriggerState } from 'react-stately';
import { CSSTransition } from 'react-transition-group';

import styles from './Modal.module.scss';

interface ModalProps extends AriaModalOverlayProps {
    state: OverlayTriggerState;
    children: ReactNode;
    visible: boolean;
    exitDelay: number;
}

const Modal = ({
    state,
    children,
    visible,
    exitDelay,
    ...props
}: ModalProps) => {
    const underlayRef = useRef(null);
    const modalRef = useRef(null);
    const { modalProps, underlayProps } = useModalOverlay(
        { isDismissable: true, ...props },
        state,
        modalRef
    );
    const { isOpen } = state;
    const [exited, setExited] = useState(!isOpen);

    if (!(isOpen || !exited)) {
        return null;
    }

    return (
        <Overlay>
            <CSSTransition
                in={visible}
                appear
                onEntered={() => setExited(false)}
                onExited={() => setExited(true)}
                timeout={{ appear: 0, exit: exitDelay }}
                classNames={{
                    appear: styles['modal__animation-overlay--appear'],
                    appearDone: styles['modal__animation-overlay--appear-done'],
                    exit: styles['modal__animation-overlay--exit'],
                    exitActive: styles['modal__animation-overlay--exit-active'],
                }}
                nodeRef={underlayRef}
            >
                <div
                    className={styles.overlay}
                    {...underlayProps}
                    ref={underlayRef}
                >
                    <CSSTransition
                        in={visible}
                        appear
                        timeout={{ appear: 0, exit: exitDelay }}
                        classNames={{
                            appear: styles['modal__animation-modal--appear'],
                            appearDone:
                                styles['modal__animation-modal--appear-done'],
                            exit: styles['modal__animation-modal--exit'],
                            exitActive:
                                styles['modal__animation-modal--exit-active'],
                        }}
                        nodeRef={modalRef}
                    >
                        <div
                            {...modalProps}
                            ref={modalRef}
                            className={styles.overlay__body}
                        >
                            {children}
                        </div>
                    </CSSTransition>
                </div>
            </CSSTransition>
        </Overlay>
    );
};

export default Modal;
