import React, { ReactElement, ReactNode, useEffect, useState } from 'react';
import {
    type AriaModalOverlayProps,
    mergeProps,
    useOverlayTrigger,
} from 'react-aria';
import {
    type OverlayTriggerProps,
    useOverlayTriggerState,
} from 'react-stately';

import Button, { ButtonProps } from '../Button/Button';

import CustomTrigger from './CustomTrigger';
import Modal from './Modal';

export interface ModalTriggerProps
    extends Omit<ButtonProps, 'children'>,
        OverlayTriggerProps,
        AriaModalOverlayProps {
    label?: ReactNode;
    children: (close: () => void) => ReactElement;
    customTrigger?: boolean;
}

const ModalTrigger = ({
    label,
    children,
    customTrigger,
    ...props
}: ModalTriggerProps) => {
    const state = useOverlayTriggerState(props);
    const { isOpen } = state;
    const { triggerProps, overlayProps } = useOverlayTrigger(
        { type: 'dialog' },
        state
    );
    const [delayedState, setDelayedState] = useState(state);
    const [visible, setVisible] = useState(false);

    // Slightly less than transition time in CSS to avoid potential flickering
    const EXIT_DELAY = 90;

    useEffect(() => {
        let delayedOpen: NodeJS.Timeout;
        // Delay opening to prevent touch event transfering inside the modal on mobile devices.
        if (isOpen === true) {
            setVisible(true);
            delayedOpen = setTimeout(() => {
                setDelayedState(state);
            }, 10);
        }
        // Delay closing until animation finishes to avoid horizontal layout shift caused by appearing scrollbar. At the same time, it prevents touch event transfering outside the modal on mobile devices.
        if (isOpen === false) {
            setVisible(false);
            delayedOpen = setTimeout(() => {
                setDelayedState(state);
            }, EXIT_DELAY);
        }

        return () => {
            clearTimeout(delayedOpen);
        };
    }, [isOpen]);

    const TriggerComponent = customTrigger ? CustomTrigger : Button;
    // Remove aria-expanded from modal button as requested in accessibility audit
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { 'aria-expanded': expanded, ...restTriggerProps } = triggerProps;
    const mergedTriggerProps = mergeProps(restTriggerProps, props);

    return (
        <>
            <TriggerComponent {...mergedTriggerProps}>{label}</TriggerComponent>
            <Modal
                {...props}
                state={delayedState}
                visible={visible}
                exitDelay={EXIT_DELAY}
            >
                {React.cloneElement(children(state.close), overlayProps)}
            </Modal>
        </>
    );
};

export default ModalTrigger;
