import React, { ReactNode, useRef } from 'react';
import {
    AriaDialogProps,
    mergeProps,
    useDialog,
    useFocusRing,
} from 'react-aria';
import classNames from 'classnames';

import Button, { ButtonProps } from '../Button/Button';
import { SvgClose } from '../Icons';
import { Heading } from '../Typography';
import type { HeadingProps } from '../Typography/types';

import styles from './Modal.module.scss';

export interface ModalDialogProps extends AriaDialogProps {
    title?: string;
    close?: () => void;
    children: ReactNode;
    footer?: ReactNode;
    className?: string;
    hasInnerScroll?: boolean;
    closeButtonAriaLabel?: string;
    closeButtonTooltipLabel?: ButtonProps['tooltipLabel'];
    titleAs?: HeadingProps['as'];
    titleVariant?: HeadingProps['variant'];
}

const ModalDialog = ({
    title,
    close,
    children,
    footer,
    className,
    hasInnerScroll,
    closeButtonAriaLabel,
    closeButtonTooltipLabel,
    titleAs,
    titleVariant = 'h3',
    ...props
}: ModalDialogProps) => {
    const ref = useRef(null);
    const { dialogProps, titleProps } = useDialog(props, ref);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.modal, className, {
        [styles['focus-ring']]: isFocusVisible,
        [styles['modal__inner-scroll']]: hasInnerScroll,
    });

    return (
        <div
            {...mergeProps(dialogProps, focusProps)}
            ref={ref}
            className={classList}
        >
            {(title || close) && (
                <div className={styles.modal__header}>
                    <div className={styles['modal__title-wrapper']}>
                        {title && (
                            <Heading
                                {...titleProps}
                                variant={titleVariant}
                                as={titleAs}
                            >
                                {title}
                            </Heading>
                        )}
                    </div>
                    {close && (
                        <Button
                            variant="tertiary"
                            onPress={close}
                            isIcon
                            aria-label={closeButtonAriaLabel}
                            tooltipLabel={closeButtonTooltipLabel}
                            tooltipWrapperClassName={
                                styles['modal__close-button']
                            }
                            className={styles['modal__close-button']}
                        >
                            <SvgClose aria-hidden="true" />
                        </Button>
                    )}
                </div>
            )}

            <div className={styles.modal__body}>{children}</div>

            {footer && <div className={styles.modal__footer}>{footer}</div>}
        </div>
    );
};

export default ModalDialog;
